.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,75);
    width: 100% ;
    background-color: white;
}

.messageSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5 ;
    padding-bottom: 15px;
    padding: 15px;
}

.messageSender__top > form{
    flex: 1;
    display: flex;
}

.messageSender__top > form > input {
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5 ; 
}

.messageSender__top > form > button {
   display: none; 
}
.messageSender__bottom{
    display: flex;
    justify-content: space-evenly;
}

.messageSender_option{
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
}

.messageSender_option > h3 {
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender_option:hover {
    background-color: #eff2f5;
    border-radius: 20px;
}